/**
 * @function styleContent
 */

'use strict'

const ReactDOM = require('react-dom/server')

/** @lends styleContent */
function styleContent (elm) {
  let markup = ReactDOM.renderToStaticMarkup(elm)
  return markup
    .replace(/<style.*?>/, '')
    .replace(/<\/style.*?>/, '')
}

module.exports = styleContent
