/**
 * Style for ApRecover.
 * @class ApRecoverStyle
 */

'use strict'

import React, { PropTypes as types } from 'react'
import { ApStyle } from 'apeman-react-style'

/** @lends ApRecoverStyle */
const ApRecoverStyle = React.createClass({
  propTypes: {
    style: types.object,
    highlightColor: types.string
  },
  getDefaultProps () {
    return {
      style: {},
      highlightColor: ApStyle.DEFAULT_HIGHLIGHT_COLOR,
      backgroundColor: ApStyle.DEFAULT_BACKGROUND_COLOR
    }
  },
  statics: {
    styleData (config) {
      let { highlightColor, backgroundColor } = config
      let lineHeight = 32
      let padding = 4
      let borderRadius = 4
      return {
        all: {
          '.ap-recover': {},

          '.ap-recover-oneline': {
            display: 'flex',
            width: '100%',
            boxSizing: 'border-box'
          },
          '.ap-recover-oneline .ap-text-wrap,.ap-recover-oneline .ap-password-wrap': {
            margin: 0
          },
          '.ap-recover-oneline-input': {
            width: '100%',
            lineHeight: `${lineHeight}px`,
            boxSizing: 'border-box',
            padding: 0
          },
          '.ap-recover-oneline-input .ap-text-wrap': {
            padding: 0
          },
          '.ap-recover-oneline-input input': {
            lineHeight: `${lineHeight - padding * 2}px`,
            padding: `${padding}px ${padding * 2}px`,
            boxSizing: 'border-box',
            margin: 0,
            borderRight: 'none',
            borderRadius: `${borderRadius}px 0 0 ${borderRadius}px`
          },
          '.ap-recover-oneline-button': {
            minWidth: '80px',
            padding: '0 16px',
            textAlign: 'center',
            margin: 0,
            lineHeight: `${lineHeight}px`,
            flexShrink: 0,
            boxSizing: 'border-box',
            borderRadius: `0 ${borderRadius}px ${borderRadius}px 0`
          },
          '.ap-recover-fake-input': {
            display: 'none !important'
          },
          '.ap-recover-send-message': {
            display: 'block'
          },
          '.ap-recover-send-message-none': {
            display: 'none'
          },
          '.ap-recover-reset-message': {
            display: 'block'
          },
          '.ap-recover-reset-message-none': {
            display: 'none'
          }
        }
      }
    }
  },
  render () {
    const s = this
    let { props } = s

    let { all, small, medium, large } = ApRecoverStyle.styleData(props)

    return (
      <ApStyle data={ Object.assign(all, props.style) }
               smallMediaData={ small }
               mediumMediaData={ medium }
               largeMediaData={ large }
      >{ props.children }</ApStyle>
    )
  }
})

export default ApRecoverStyle
