/**
 * Image component
 * @class ApAlbumImage
 */

'use strict'

import React, {PropTypes as types} from 'react'
import classnames from 'classnames'
import {ApImage} from 'apeman-react-image'
import {ApPureMixin} from 'apeman-react-mixin-pure'

/** @lends ApAlbumImage */
const ApAlbumImage = React.createClass({

  // --------------------
  // Specs
  // --------------------

  propTypes: {},

  mixins: [
    ApPureMixin
  ],

  statics: {},

  getInitialState () {
    return {}
  },

  getDefaultProps () {
    return {}
  },

  render () {
    const s = this
    let { state, props } = s

    return (
      <ApImage { ...props }
        className={ classnames('ap-album-image', props.className) }
        style={ Object.assign({}, props.style) }>
      </ApImage>
    )
  }

  // --------------------
  // Lifecycle
  // --------------------

  // ------------------
  // Custom
  // ------------------

  // ------------------
  // Private
  // ------------------
})

export default ApAlbumImage
