/**
 * Style for ApResign.
 * @class ApResignStyle
 */

'use strict'

import React, { Component, PropTypes as types } from 'react'
import { ApStyle } from 'apeman-react-style'

/** @lends ApResignStyle */
class ApResignStyle extends Component {
  render () {
    const s = this
    let { props } = s

    let { all, small, medium, large } = ApResignStyle.styleData(props)

    return (
      <ApStyle data={ Object.assign(all, props.style) }
               smallMediaData={ small }
               mediumMediaData={ medium }
               largeMediaData={ large }
      >{ props.children }</ApStyle>
    )
  }

}

Object.assign(ApResignStyle, {
  propTypes: {
    style: types.object,
    highlightColor: types.string
  },
  defaultProps: {
    style: {},
    highlightColor: ApStyle.DEFAULT_HIGHLIGHT_COLOR,
    backgroundColor: ApStyle.DEFAULT_BACKGROUND_COLOR
  },
  styleData (config) {
    let { highlightColor, backgroundColor } = config
    return {
      all: {
        '.ap-resign': {},
        '.ap-resign-lead-form-title': {
          fontSize: '1.5em'
        },
        '.ap-resign-lead-form-message': {},
        '.ap-resign-reason-form-title': {
          fontSize: '1.5em'
        },
        '.ap-resign-lead-form-message-field': {
          margin: '8px 0'
        },
        '.ap-resign-lead-form-button-field': {
          margin: '16px 0 8px'
        },
        '.ap-resign-reason-form-message': {},
        '.ap-resign-lead-form-cancel-button .ap-prev-button-icon': {
          display: 'none'
        },
        '.ap-resign-reason-form-button-field': {
          margin: '16px 0 8px'
        }
      }

    }
  }
})

export default ApResignStyle
