/**
 * Album component.
 * @class ApAlbum
 */

'use strict'

import React, { PropTypes as types } from 'react'
import ReactDOM from 'react-dom'
import classnames from 'classnames'
import uuid from 'uuid'
import { ApLayoutMixin } from 'apeman-react-mixin-layout'
import { ApScroll } from 'apeman-react-scroll'
import { ApButton, ApIconButton } from 'apeman-react-button'
import { ApDraggable } from 'apeman-react-draggable'
import rangecal from 'rangecal'
import ApAlbumImage from './ap_album_image'

const DEFAULT_LEFT_ICON = 'fa fa-caret-left'
const DEFAULT_RIGHT_ICON = 'fa fa-caret-right'

/** @lends ApAlbum */
const ApAlbum = React.createClass({

  // --------------------
  // Specs
  // --------------------

  propTypes: {
    /** Image source URLs */
    images: types.array.isRequired,
    /** Size of thumbnail image */
    thumbnailSize: types.number,
    /** Icon for left pager button */
    leftIcon: types.string,
    /** Icon for right pager button */
    rightIcon: types.string,
    /** Image scale policy */
    scale: types.string,
    /** Component width */
    width: types.oneOfType([ types.number, types.string ]),
    /** Component height */
    height: types.oneOfType([ types.number, types.string ])
  },

  mixins: [
    ApLayoutMixin
  ],

  statics: {
    DEFAULT_LEFT_ICON,
    DEFAULT_RIGHT_ICON
  },

  getInitialState () {
    return {
      selected: 0,
      dragging: false
    }
  },

  getDefaultProps () {
    return {
      images: [],
      thumbnailSize: 48,
      leftIcon: DEFAULT_LEFT_ICON,
      rightIcon: DEFAULT_RIGHT_ICON,
      id: `ap-album-${uuid.v4()}`,
      scale: 'fill',
      width: '420px',
      height: '360px'
    }
  },

  render () {
    const s = this
    let { state, props, layouts } = s

    let { selected } = state
    let {
      id,
      thumbnailSize,
      leftIcon, rightIcon,
      width, height
    } = props

    let images = s.getImages()

    let mainWidth = layouts.main.width
    let mainHeight = layouts.main.height
    let x = mainWidth * selected * -1
    let y = 0

    return (
      <div id={ id }
           className={ classnames('ap-album', props.className) }
           style={ Object.assign({ width, height }, props.style) }>

        <ApIconButton className={ classnames('ap-album-button', 'ap-album-prev-button', {
          'ap-album-button-hidden': selected === 0
        }) }
                      data={ -1 }
                      icon={ leftIcon }
                      simple={ true }
                      onTap={ s.handlePagerTap }
                      style={ { height: mainHeight } }
        />
        <ApScroll className={ classnames('ap-album-footer', {
          'ap-album-footer-mounted': s.mounted
        }) }
                  style={ { height: thumbnailSize, width: '100%' } }
                  direction='x'
                  ref='footer'>
          <ul className='ap-album-thumbnail-list'
              style={ { height: thumbnailSize, width: thumbnailSize * images.length } }>
            {
              images.map((image, i) => (
                <li className={ classnames('ap-album-thumbnail-item', {
                  'ap-album-thumbnail-item-selected': state.selected === i
                }) }
                    key={ `${image.src}-${i}` }>
                  <ApButton className='ap-album-thumbnail-button'
                            simple={ true }
                            data={ i }
                            onTap={ s.handleThumbnailTap }
                  >
                    <ApAlbumImage className='ap-album-thumbnail-image'
                                  width={ thumbnailSize }
                                  height={ thumbnailSize - 2 }
                                  scale='fill'
                                  alt={ image.alt }
                                  src={ image.src }
                    />
                  </ApButton>
                </li>
              ))
            }

          </ul>
        </ApScroll>
        <ApDraggable className='ap-album-body'
                     maxX={ 0 }
                     minX={ mainWidth * images.length * -1 }
                     x={ x }
                     y={ y }
                     direction='HORIZONTAL'
                     ref='body'
                     onStart={ s.handleDragStart }
                     onStop={ s.handleDragStop }
        >
          <div className='ap-album-main-image-wrap'
               style={ { width: `${images.length * 100}%` } }>
            {
              images.map((image, i) => (
                <ApAlbumImage className='ap-album-main-image'
                              key={ `${image.src}-${i}` }
                              src={ image.src }
                              alt={ image.alt }
                              scale={ props.scale }
                              width={ mainWidth }
                              height={ mainHeight }
                />
              ))
            }

          </div>
        </ApDraggable>
        <ApIconButton className={ classnames('ap-album-button', 'ap-album-next-button', {
          'ap-album-button-hidden': selected === images.length - 1
        }) }
                      data={ 1 }
                      icon={ rightIcon }
                      simple={ true }
                      onTap={ s.handlePagerTap }
                      style={ { height: mainHeight } }
        />


      </div>
    )
  },

  // --------------------
  // Lifecycle
  // --------------------

  componentDidMount () {
    const s = this
    s.mounted = true
    setTimeout(() => {
      if (!s.mounted) {
        return
      }
      s.layout()
    }, 0)
  },

  componentWillUnmount () {
    const s = this
    s.mounted = false
  },

  // --------------------
  // Handle
  // --------------------

  handleDragStart (e) {
    const s = this
    s.setState({
      dragging: true
    })
  },

  handleDragStop (e) {
    const s = this
    let { layouts, state } = s
    let { x, y } = e.detail
    let mainWidth = layouts.main.width
    let rate = (mainWidth / 2 + (-1 * x)) / mainWidth
    let floating = rate - parseInt(rate)
    let amount = parseInt(rate) - state.selected
    if (floating > 0.66) {
      amount += 1
    }
    if (floating < 0.33) {
      amount -= 1
    }
    s.setState({
      dragging: false
    })
    s.flipPage(amount)
  },

  handlePagerTap (e) {
    const s = this
    let amount = e.data
    s.flipPage(amount)
  },

  handleThumbnailTap (e) {
    const s = this
    s.setState({
      selected: e.data
    })
  },

  // ------------------
  // For ApLayoutMixin
  // ------------------

  getInitialLayouts () {
    const s = this
    return {
      main: {
        width: '100%',
        height: 'auto'
      }
    }
  },

  calcLayouts () {
    const s = this
    let node = ReactDOM.findDOMNode(s).getBoundingClientRect()
    let footer = ReactDOM.findDOMNode(s.refs[ 'footer' ]).getBoundingClientRect()

    let width = node.width
    let height = node.height - footer.height
    return {
      main: {
        width, height
      }
    }
  },

  // ------------------
  // Custom
  // ------------------

  getImages () {
    const s = this
    return (s.props.images || []).map((image) => {
      if (typeof image === 'string') {
        return {
          src: image,
          alt: null
        }
      }
      return image
    })
  },

  flipPage (amount) {
    const s = this
    let { state, props } = s
    let selected = state.selected + amount
    s.setState({
      selected: rangecal.round(0, s.getImages().length - 1, selected)
    })
  }

  // ------------------
  // Private
  // ------------------
})

export default ApAlbum
