/**
 * Style for ApProfile.
 * @class ApProfileStyle
 */

'use strict'

import React, {Component, PropTypes as types} from 'react'
import {ApStyle} from 'apeman-react-style'

/** @lends ApProfileStyle */
class ApProfileStyle extends Component {
  render () {
    const s = this
    let { props } = s

    let { all, small, medium, large } = ApProfileStyle.styleData(props)

    return (
      <ApStyle data={ Object.assign(all, props.style) }
               smallMediaData={ small }
               mediumMediaData={ medium }
               largeMediaData={ large }
      >{ props.children }</ApStyle>
    )
  }
}

Object.assign(Component, {
  propTypes: {
    style: types.object,
    highlightColor: types.string
  },

  defaultProps: {
    style: {},
    highlightColor: ApStyle.DEFAULT_HIGHLIGHT_COLOR,
    backgroundColor: ApStyle.DEFAULT_BACKGROUND_COLOR
  },

  styleData (config) {
    let { highlightColor, backgroundColor } = config
    return {
      all: {
        '.ap-profile': {},
        '.ap-profile-lead-form-message': {
          fontSize: '1em',
          color: '#AAA',
          padding: '32px 16px'
        }
      }
    }
  }
})

export default ApProfileStyle
