/**
 * Form for resign reason.
 * @class ApResignReasonForm
 */

'use strict'

import React, {Component, PropTypes as types} from 'react'
import classnames from 'classnames'

import {ApForm, withForm} from 'apeman-react-form'
import {ApText} from 'apeman-react-text'
import {ApFieldSet, ApField} from 'apeman-react-field'
import {ApNextButton, ApPrevButton} from 'apeman-react-button'
import {clone} from 'asobj'

/** @lends ApResignReasonForm */
class ApResignReasonForm extends Component {

  // --------------------
  // Specs
  // --------------------
  static get defaultProps () {
    return {
      labels: {
        title: 'Please tell us the reason.',
        message: 'This information will be used to improve our Service.',
        submit: 'Commit Withdrawal',
        cancel: 'Back',
        content: 'Reason'
      }
    }
  }

  render () {
    const s = this
    let { props } = s
    let {
      idOf, labels, values,
      onUpdate,
      onSubmit, onCancel
    } = props
    let formProps = clone(props, {
      without: [
        'errors', 'l', 'onSubmit', 'onCancel',
        'idOf', 'onUpdate', 'labels'
      ]
    })
    return (
      <ApForm { ...formProps }
              className={ classnames('ap-resign-reason-form', props.className)}
              style={Object.assign({}, props.style)}
              centered
      >
        <ApFieldSet plain>
          < ApResignReasonForm.TitleField { ...{ idOf, labels } } />
          < ApResignReasonForm.MessageField { ...{ idOf, labels } }/>
          < ApResignReasonForm.CustomFiled { ...{ idOf, labels } }>
            { props.children }
          </ApResignReasonForm.CustomFiled>
          < ApResignReasonForm.ContentField { ...{ idOf, labels, values, onUpdate } }/>
          < ApResignReasonForm.SubmitField { ...{ idOf, labels, onSubmit, onCancel } }/>
        </ApFieldSet>
      </ApForm>
    )
  }

  // --------------------
  // Lifecycle
  // --------------------

  // ------------------
  // Custom
  // ------------------

  // ------------------
  // Private
  // ------------------
  static TitleField ({ idOf, labels }) {
    const name = 'title'
    return (
      <ApField center className='ap-resign-reason-form-title-field'>
        <div className='ap-resign-reason-form-title'
             id={ idOf(name) }>{ labels[ name ] } </div>
      </ApField>
    )
  }

  static MessageField ({ idOf, labels }) {
    const name = 'message'
    return (
      <ApField center className='ap-resign-reason-form-message-field'>
        <div className='ap-resign-reason-form-message'
             id={ idOf(name) }>{ labels[ name ] } </div>
      </ApField>
    )
  }

  static CustomFiled ({ children }) {
    return (
      <ApField center className='ap-resign-reason-form-custom-field'>
        { children }
      </ApField>
    )
  }

  static ContentField ({ idOf, values, onUpdate }) {
    const name = 'content'
    return (
      <ApField center className='ap-resign-reason-form-text-field'>
        <ApText id={ idOf(name) }
                name={ name }
                value={ values[ name ] }
                rows={ 8 }
                onChange={ ({ target }) => onUpdate({ [name]: target.value })}
        />
      </ApField>
    )
  }

  static SubmitField ({ idOf, labels, onCancel, onSubmit }) {
    const name = 'submit'
    return (
      <ApField center className='ap-resign-reason-form-button-field'>
        <ApPrevButton onTap={ onCancel }
                      className='ap-resign-reason-form-cancel-button'
                      id={ idOf('cancel') }
                      primary={ false }
        >{ labels[ 'cancel' ] }</ApPrevButton>
        <ApNextButton onTap={ onSubmit }
                      className='ap-resign-reason-form-submit-button'
                      id={ idOf(name) }
                      primary
        >{ labels[ 'submit' ] }</ApNextButton>
      </ApField>
    )
  }
}

export default withForm(ApResignReasonForm)
