/**
 * Form component for profile.
 * @class ApProfileDataForm
 */

'use strict'

import React, {Component, PropTypes as types} from 'react'
import classnames from 'classnames'

import {ApForm, withForm} from 'apeman-react-form'
import {ApFieldSet, ApField, ApFieldLabel, ApFieldValue} from 'apeman-react-field'
import {ApButton} from 'apeman-react-button'
import {ApText} from 'apeman-react-text'
import {ApUpload} from 'apeman-react-upload'
import {autobind} from 'breact'

/** @lends ApProfileDataForm */
class ApProfileDataForm extends Component {
  // --------------------
  // Specs
  // --------------------
  constructor (props) {
    super(props)
    const s = this
    autobind(s)
  }

  render () {
    const s = this
    let { props } = s
    let {
      id,
      values,
      labels,
      placeholders,
      idOf,
      onUpdate,
      onSubmit,
      spinning,
      errorList,
      errorStyle,
      centered
    } = props

    return (
      <ApForm id={ id }
              centered={ centered }
              spinning={ spinning }
              className={ classnames('ap-profile-data-form', props.className)}
              style={ Object.assign({}, props.style) }
      >
        { errorList }
        { errorStyle }
        <ApFieldSet plain>
          <ApProfileDataForm.NameField { ...{ idOf, labels, values, placeholders, onUpdate } }/>
          <ApProfileDataForm.ImageField { ...{ idOf, labels, values, onUpdate } }/>
          <ApProfileDataForm.SubmitField { ...{ idOf, labels, onSubmit } }/>
        </ApFieldSet>
        <ApFieldSet plain>
        </ApFieldSet>
      </ApForm>
    )
  }
}

Object.assign(ApProfileDataForm, {
  defaultProps: {
    labels: {
      name: 'Display Name',
      image: 'Profile Image',
      submit: 'Save'
    },
    placeholders: {
      name: 'Full name for display'
    }
  },

  // ------------------
  // Sub Components
  // ------------------

  NameField ({ idOf, labels, values, placeholders, onUpdate }) {
    const name = 'name'
    return (
      <ApField>
        <ApFieldLabel htmlFor={ idOf(name) }>
          { labels[ name ] }
        </ApFieldLabel>
        <ApFieldValue>
          <ApText id={ idOf(name) }
                  name={ name }
                  value={ values[ name ] }
                  placeholder={ placeholders[ name ] }
                  onChange={ (e) => onUpdate({ [name]: e.target.value }) }
          />
        </ApFieldValue>
      </ApField>
    )
  },

  ImageField ({ idOf, labels, values, onUpdate }) {
    const name = 'image'
    return (
      <ApField>
        <ApFieldLabel htmlFor={ idOf(name) }>
          { labels[ name ] }
        </ApFieldLabel>
        <ApFieldValue>
          <ApUpload id={ idOf(name) }
                    name={ name }
                    value={ values[ name ] }
                    multiple={ false }
                    accept="image/*"
                    onLoad={ ({ urls }) => onUpdate({ [name]: urls[ 0 ] }) }
          />
        </ApFieldValue>
      </ApField>
    )
  },

  SubmitField ({ idOf, labels, onSubmit }) {
    const name = 'submit'
    return (
      <ApField center
               className="ap-profile-data-form-button-field">
        <ApButton onTap={ onSubmit }
                  wide
                  id={ idOf(name) }
                  primary
        >{ labels[ name ] }</ApButton>
      </ApField>
    )
  }
})

export default ApProfileDataForm
export default withForm(ApProfileDataForm)
