/**
 * @function renderStyleData
 */
'use strict'

const { ApStyle } = require('apeman-react-style')
const { EOL } = require('os')
/** @lends renderStyleData */
function renderStyleData (data) {
  let {
    stringifyStyle,
    stringifyMediaStyle,
    SMALL_MEDIA_BREAKPOINT,
    MEDIUM_MEDIA_BREAKPOINT,
    LARGE_MEDIA_BREAKPOINT
  } = ApStyle

  let { all, small, medium, large } = data
  return [
    stringifyStyle(all),
    stringifyMediaStyle(small, SMALL_MEDIA_BREAKPOINT),
    stringifyMediaStyle(medium, MEDIUM_MEDIA_BREAKPOINT),
    stringifyMediaStyle(large, LARGE_MEDIA_BREAKPOINT)
  ].filter((style) => !!style).join(EOL)
}

module.exports = renderStyleData
