/**
 * Form for resign lead.
 * @class ApResignLeadForm
 */

'use strict'

import React, { Component, PropTypes as types } from 'react'
import classnames from 'classnames'

import { ApForm, withForm } from 'apeman-react-form'
import { ApFieldSet, ApField } from 'apeman-react-field'
import { ApNextButton, ApPrevButton } from 'apeman-react-button'

/** @lends ApResignLeadForm */
class ApResignLeadForm extends Component {

  // --------------------
  // Specs
  // --------------------
  static get defaultProps () {
    return {
      labels: {
        title: 'Are you sure ?',
        message: 'Once delete the account, there is no going back.',
        submit: 'Continue to withdraw',
        cancel: 'Cancel'
      }
    }
  }

  render () {
    const s = this
    let { props } = s
    let {
      id,
      idOf,
      labels,
      onSubmit,
      onCancel
    } = props
    return (
      <ApForm id={ id }
              className={ classnames('ap-resign-lead-form', props.className)}
              style={Object.assign({}, props.style)}
              centered
      >
        <ApFieldSet plain>
          <ApResignLeadForm.TitleField { ...{ idOf, labels } }/>
          <ApResignLeadForm.MessageField { ...{ idOf, labels } }/>
          <ApResignLeadForm.CustomFiled >
          </ApResignLeadForm.CustomFiled>
          <ApResignLeadForm.SubmitField { ...{ idOf, labels, onCancel, onSubmit } }/>
        </ApFieldSet>
      </ApForm>
    )
  }

  // --------------------
  // Lifecycle
  // --------------------

  // ------------------
  // Custom
  // ------------------

  // ------------------
  // Private
  // ------------------

  static TitleField ({ idOf, labels, title }) {
    const name = 'title'
    return (
      <ApField center className='ap-resign-lead-form-title-field'>
        <div className='ap-resign-lead-form-title'
             id={ idOf(name) }>{ labels[ name ] }</div>
      </ApField>
    )
  }

  static MessageField ({ idOf, labels }) {
    const name = 'message'
    return (
      <ApField center className='ap-resign-lead-form-message-field'>
        <div className='ap-resign-lead-form-message'
             id={ idOf(name) }>{ labels[ name ] }</div>
      </ApField>
    )
  }

  static CustomFiled ({ idOf, children }) {
    return (
      <ApField center className='ap-resign-lead-form-custom-field'>
        { children }
      </ApField>
    )
  }

  static SubmitField ({ idOf, labels, onCancel, onSubmit }) {
    const name = 'submit'
    return (
      <ApField center className='ap-resign-lead-form-button-field'>
        <ApPrevButton onTap={ () => onCancel() }
                      className='ap-resign-lead-form-cancel-button'
                      id={ idOf('cancel') }
                      primary={ false }
        >{ labels[ 'cancel' ] }</ApPrevButton>
        <ApNextButton onTap={ onSubmit }
                      className='ap-resign-lead-form-submit-button'
                      id={ idOf(name) }
                      primary={ false }
        >{ labels[ name ] }</ApNextButton>
      </ApField>
    )
  }
}

export default withForm(ApResignLeadForm)
