/** * Style for ApTheme.
 * @class ApThemeStyle
 */

'use strict'

import React, {Component, PropTypes as types} from 'react'
import {ApStyle} from 'apeman-react-style'
import ApBasicThemeStyle from './ap_basic_theme_style'
import ApHandyThemeStyle from './ap_handy_theme_style'

/** @lends ApThemeStyle */
class ApThemeStyle extends Component {
  render () {
    const s = this
    let { props } = s

    let data = {
      '.ap-theme': {}
    }
    let smallMediaData = {}
    let mediumMediaData = {}
    let largeMediaData = {}

    let themedStyle = [
      ApBasicThemeStyle.css(props),
      ApHandyThemeStyle.css(props)
    ].join('')

    return (
      <ApStyle data={ Object.assign(data, props.style) }
               smallMediaData={ smallMediaData }
               mediumMediaData={ mediumMediaData }
               largeMediaData={ largeMediaData }
      >
        { themedStyle }
        { props.children }
      </ApStyle>
    )
  }
}

Object.assign(ApThemeStyle, {
  propTypes: {
    style: types.object,
    dominant: types.string,
    subordinate: types.string,
    accent: types.string
  },
  defaultProps: {
    style: {},
    dominant: '#38E',
    subordinate: '#FFF',
    accent: '#FF3'
  }
})

export default ApThemeStyle
