/**
 * Form component for profile.
 * @class ApProfileLeadForm
 */

'use strict'

import React, {Component, PropTypes as types} from 'react'
import classnames from 'classnames'

import {ApForm, withForm} from 'apeman-react-form'
import {ApFieldSet, ApField} from 'apeman-react-field'
import {ApButton} from 'apeman-react-button'

/** @lends ApProfileLeadForm */
class ApProfileLeadForm extends Component {
  // --------------------
  // Specs
  // --------------------

  render () {
    const s = this
    let { props } = s
    let {
      id,
      idOf,
      labels,
      onSubmit,
      centered,
      spinning
    } = props

    return (
      <ApForm id={ id }
              centered={ centered }
              spinning={ spinning }
              className={ classnames('ap-profile-lead-form', props.className)}
              style={ Object.assign({}, props.style) }
      >
        <ApFieldSet plain>
          <ApProfileLeadForm.MessageField { ...{ idOf, labels } }/>
          <ApProfileLeadForm.SubmitField { ...{ idOf, labels, onSubmit } }/>
        </ApFieldSet>
      </ApForm>
    )
  }
}

Object.assign(ApProfileLeadForm, {
  defaultProps: {
    prefix: 'profile-lead',
    labels: {
      message: 'Your profile is not registered yet.',
      submit: 'Input Profile Now'
    }
  },

  // ------------------
  // Sub Components
  // ------------------

  MessageField ({ idOf, labels }) {
    const name = 'message'
    return (
      <ApField center className="ap-profile-lead-form-message-field">
        <div className="ap-profile-lead-form-message"
             id={ idOf(name) }>{ labels[ name ] }</div>
      </ApField>
    )
  },

  SubmitField ({ idOf, labels, onSubmit }) {
    const name = 'submit'
    return (
      <ApField center className="ap-profile-lead-form-button-field">
        <ApButton onTap={ onSubmit }
                  wide
                  id={ idOf(name) }
                  primary={ false }
        >{ labels[ name ] }</ApButton>
      </ApField>
    )
  }

})

export default ApProfileLeadForm
export default withForm(ApProfileLeadForm)
