/**
 * Form for sending recover email.
 * @class ApRecoverSendForm
 */

'use strict'

import React, { PropTypes as types } from 'react'
import classnames from 'classnames'
import { ApForm, withForm } from 'apeman-react-form'
import { ApButton } from 'apeman-react-button'
import { ApPassword } from 'apeman-react-password'
import { ApField } from 'apeman-react-field'

/** @lends ApRecoverSendForm */
const ApRecoverSendForm = React.createClass({

  // --------------------
  // Specs
  // --------------------

  propTypes: {
    labels: types.object,
    placeholders: types.object
  },

  mixins: [],

  statics: {},

  getInitialState () {
    return {}
  },

  getDefaultProps () {
    return {
      labels: {
        submit: 'Save'
      },
      placeholders: {
        password: 'New Password'
      }
    }
  },

  render () {
    const s = this
    let { props } = s
    let {
      id,
      idOf,
      labels,
      placeholders,
      values,
      onUpdate,
      onSubmit,
      centered,
      spinning,
      errorList,
      errorStyle
    } = props
    return (
      <ApForm id={ id }
              spinning={ spinning }
              centered={ centered }
              className={ classnames('ap-recover-reset-form', props.className)}
              style={ Object.assign({}, props.style) }
      >
        { errorList}
        { errorStyle }
        <p
          className={ classnames('ap-recover-reset-message', { 'ap-recover-reset-message-none': !labels[ 'message' ] }) }
          id={ idOf('message') }>
          { labels[ 'message' ] }
        </p>
        <ApField>
          <div className='ap-recover-oneline'>
            <span className='ap-recover-oneline-input'>
              <ApPassword id={ idOf('password') }
                          name={ 'password' }
                          value={ values[ 'password' ] }
                          onChange={ (e) => onUpdate({ password: e.target.value }) }
                          onEnter={ () => onSubmit(s.props.values) }
                          placeholder={ placeholders[ 'password' ] }
                          autoFocus
              />
            </span>
            <ApButton primary
                      onTap={ () => onSubmit(s.props.values) }
                      className='ap-recover-oneline-button'>
              { labels[ 'submit' ] }
            </ApButton>
          </div>
        </ApField>
        { props.children }
      </ApForm>
    )
  }
})

export default withForm(ApRecoverSendForm)
