/**
 * Form for sending recover email.
 * @class ApRecoverSendForm
 */

'use strict'

import React, { PropTypes as types } from 'react'
import classnames from 'classnames'
import { ApForm, withForm } from 'apeman-react-form'
import { ApButton } from 'apeman-react-button'
import { ApText } from 'apeman-react-text'
import { ApField } from 'apeman-react-field'

/** @lends ApRecoverSendForm */
const ApRecoverSendForm = React.createClass({

  // --------------------
  // Specs
  // --------------------

  propTypes: {},

  mixins: [],

  statics: {},

  getInitialState () {
    const s = this
    return {}
  },

  getDefaultProps () {
    return {
      labels: {
        message: null,
        submit: 'Send'
      },
      placeholders: {
        email: 'Email'
      }
    }
  },

  render () {
    const s = this
    let { props } = s
    let {
      id,
      idOf,
      labels,
      placeholders,
      values,
      onUpdate,
      onSubmit,
      centered,
      spinning,
      errorList,
      errorStyle
    } = props
    return (
      <ApForm id={ id }
              spinning={ spinning }
              centered={ centered }
              className={ classnames('ap-recover-send-form', props.className)}
              style={ Object.assign({}, props.style) }
      >
        { errorList}
        { errorStyle }
        <p className={ classnames('ap-recover-send-message', {
          'ap-recover-send-message-none': !labels[ 'message' ]
        }) }>
          { labels[ 'message' ] }
        </p>
        <ApField>
          <div className='ap-recover-oneline'>
            <span className='ap-recover-oneline-input'>
              <ApText id={ idOf('email') }
                      name={ 'email' }
                      value={ values[ 'email' ] }
                      onChange={ (e) => onUpdate({ email: e.target.value }) }
                      onEnter={ () => onSubmit(s.props.values) }
                      placeholder={ placeholders[ 'email' ] }
                      autoFocus
              />
            </span>
            <ApButton primary
                      id={ idOf('submit') }
                      onTap={ () => onSubmit(s.props.values) }
                      className='ap-recover-oneline-button'>
              { labels[ 'submit' ] }
            </ApButton>
          </div>
        </ApField>
        { props.children }
      </ApForm>
    )
  }
})

export default withForm(ApRecoverSendForm)
