/**
 * Style for ApAlbum.
 * @class ApAlbumStyle
 */

'use strict'

import React, { PropTypes as types } from 'react'
import { ApStyle } from 'apeman-react-style'

/** @lends ApAlbumStyle */
const ApAlbumStyle = React.createClass({
  propTypes: {
    style: types.object,
    highlightColor: types.string
  },
  getDefaultProps () {
    return {
      style: {},
      highlightColor: ApStyle.DEFAULT_HIGHLIGHT_COLOR,
      backgroundColor: ApStyle.DEFAULT_BACKGROUND_COLOR
    }
  },
  statics: {
    styleData (config) {
      let { highlightColor, backgroundColor } = config
      let borderColor = '#888'
      return {
        all: {
          '.ap-album': {
            position: 'relative',
            minHeight: 148,
            minWidth: 296,
            display: 'inline-flex',
            overflow: 'hidden',
            flexDirection: 'column-reverse',
            border: `1px solid ${borderColor}`
          },
          '.ap-album .ap-image': {
            background: 'transparent'
          },
          '.ap-album-body': {
            position: 'relative',
            transition: 'transform 80ms'
          },
          '.ap-album-button': {
            margin: 0,
            padding: 0,
            position: 'absolute',
            top: 0,
            bottom: 0,
            display: 'inline-flex',
            justifyContent: 'center',
            alignItems: 'center',
            width: 38,
            boxSizing: 'border-box',
            background: 'rgba(255,255,255,0.2)',
            zIndex: 44,
            color: '#AAA'
          },
          '.ap-album-button:hover': {
            cursor: 'pointer',
            textDecoration: 'none'
          },
          '.ap-album-button:active': {
            opacity: 0.9
          },
          '.ap-album-button-hidden': {
            display: 'none'
          },
          '.ap-album-prev-button': {
            left: 0
          },
          '.ap-album-next-button': {
            right: 0
          },
          '.ap-album-footer': {
            margin: '-1px',
            border: `1px solid ${borderColor}`,
            visibility: 'hidden',
            flexShrink: '0'
          },
          '.ap-album-footer-mounted': {
            visibility: 'visible'
          },
          '.ap-album-main-image-wrap': {
            padding: 0,
            margin: 0,
            whiteSpace: 'nowrap',
            display: 'block'
          },
          '.ap-album-main-image': {
            display: 'inline-flex',
            WebkitUserSelect: 'none',
            pointerEvents: 'none',
            userSelect: 'none'
          },
          '.ap-album-thumbnail-list': {
            padding: 0,
            margin: '0 -1px',
            whiteSpace: 'nowrap',
            display: 'block'
          },
          '.ap-album-thumbnail-item': {
            listStyle: 'none',
            display: 'inline-block',
            boxSizing: 'border-box',
            fontSize: 9,
            margin: '0 -1px 0 0',
            position: 'relative',
            border: `1px solid ${borderColor}`
          },
          '.ap-album-thumbnail-item-selected': {
            borderColor: `${highlightColor}`,
            zIndex: 4
          },
          '.ap-album-thumbnail-image': {},
          '.ap-album-thumbnail-button': {
            padding: 0,
            margin: 0
          }
        }
      }
    }
  },
  render () {
    const s = this
    let { props } = s

    let { all, small, medium, large } = ApAlbumStyle.styleData(props)

    return (
      <ApStyle data={ Object.assign(all, props.style) }
               smallMediaData={ small }
               mediumMediaData={ medium }
               largeMediaData={ large }
      >{ props.children }</ApStyle>
    )
  }
})

export default ApAlbumStyle
