/**
 * Basic style for ApTheme.
 * @class ApHandyThemeStyle
 */

'use strict'

import React, {PropTypes as types} from 'react'
import handy from 'apeman-react-handy'
import {ApStyle} from 'apeman-react-style'
import renderStyleData from './styling/render_style_data'

/** @lends ApHandyThemeStyle */
const ApHandyThemeStyle = React.createClass({
  propTypes: {
    style: types.object,
    dominant: types.string.isRequired,
    subordinate: types.string.isRequired,
    accent: types.string.isRequired
  },
  getDefaultProps () {
    return {
      style: {}
    }
  },
  statics: {
    css (config) {
      let { dominant, subordinate, accent } = config
      return [
        handy.ApSignStyle,
        handy.ApRecoverStyle,
        handy.ApProfileStyle,
        handy.ApResignStyle
      ]
        .map((style) => {
          if (typeof style === 'string') {
            return style
          }
          let props = Object.assign({}, style.getDefaultProps && style.getDefaultProps() || style.defaultProps, {
            highlightColor: dominant, backgroundColor: subordinate
          })
          return renderStyleData(style.styleData(props))
        })
        .join('')
    }
  },
  render () {
    const s = this
    let { props } = s

    let { dominant, subordinate, accent } = props

    let data = {
      '.ap-handy-theme': {}
    }
    let smallMediaData = {}
    let mediumMediaData = {}
    let largeMediaData = {}

    return (
      <ApStyle data={ Object.assign(data, props.style) }
               smallMediaData={ smallMediaData }
               mediumMediaData={ mediumMediaData }
               largeMediaData={ largeMediaData }
      >
        { ApHandyThemeStyle.css(props) }
        { props.children }
      </ApStyle>
    )
  }
})

export default ApHandyThemeStyle
