/**
 * Basic style for ApTheme.
 * @class ApBasicThemeStyle
 */

'use strict'

import React, {PropTypes as types} from 'react'
import * as basic from 'apeman-react-basic'
import {ApStyle} from 'apeman-react-style'
import renderStyleData from './styling/render_style_data'
import styleContent from './styling/style_content'

/** @lends ApBasicThemeStyle */
const ApBasicThemeStyle = React.createClass({
  propTypes: {
    style: types.object,
    dominant: types.string.isRequired,
    subordinate: types.string,
    accent: types.string
  },
  getDefaultProps () {
    return {
      style: {},
      dominant: '#38E',
      subordinate: '#FFF',
      accent: '#EE3'
    }
  },
  statics: {
    css (config) {
      let { dominant, subordinate, accent } = config

      return [
        basic.ApContainerStyle,
        basic.ApTabStyle,
        basic.ApIconStyle,
        basic.ApSpinnerStyle,
        basic.ApImageStyle,
        basic.ApNoteStyle,
        basic.ApTagStyle,
        basic.ApListStyle,
        basic.ApButtonStyle,
        basic.ApCheckboxStyle,
        basic.ApRadioStyle,
        basic.ApArticleStyle,
        basic.ApSectionStyle,
        basic.ApAsideStyle,
        basic.ApTouchableStyle,
        basic.ApDraggableStyle,
        basic.ApTextStyle,
        basic.ApSelectStyle,
        basic.ApPasswordStyle,
        basic.ApSearchStyle,
        basic.ApUploadStyle,
        basic.ApToggleStyle,
        basic.ApLinksStyle,
        basic.ApScrollStyle,
        basic.ApMenuStyle,
        basic.ApInfoStyle,
        basic.ApFrameStyle,
        basic.ApCaptchaStyle,
        basic.ApFieldStyle,
        basic.ApToastStyle,
        basic.ApBodyStyle,
        basic.ApDropdownStyle,
        basic.ApHeaderStyle,
        basic.ApMainStyle,
        basic.ApFooterStyle,
        basic.ApDialogStyle,
        basic.ApYesnoDialogStyle,
        basic.ApConfirmDialogStyle,
        basic.ApFormStyle,
        basic.ApNoticeStyle,
        basic.ApSliderStyle,
        basic.ApRangeStyle,
        basic.ApAccordionStyle,
        basic.ApPageStyle,
        basic.ApJumbotronStyle,
        basic.ApViewStyle,
        basic.ApViewStackStyle,
        basic.ApDoneStyle,
        basic.ApDevtoolStyle,
        basic.ApGridStyle,
        basic.ApCardStyle,
        basic.ApCatalogStyle,
        basic.ApSwitchStyle,
        basic.ApTableStyle,
        basic.ApPagerStyle,
        basic.ApChatStyle
      ]
        .map((style) => {
          if (typeof style === 'string') {
            return style
          }
          let props = Object.assign({}, style.getDefaultProps && style.getDefaultProps() || style.defaultProps, {
            highlightColor: dominant, backgroundColor: subordinate
          })
          if (!style.styleData) {
            throw new Error(`.styleData() is not implemented on ${style.displayName}`)
          }
          return renderStyleData(style.styleData(props))
        })
        .join('')
    }
  },
  render () {
    const s = this
    let { props } = s

    let data = {
      '.ap-basic-theme': {}
    }
    let smallMediaData = {}
    let mediumMediaData = {}
    let largeMediaData = {}

    return (
      <ApStyle data={ Object.assign(data, props.style) }
               smallMediaData={ smallMediaData }
               mediumMediaData={ mediumMediaData }
               largeMediaData={ largeMediaData }
      >
        { ApBasicThemeStyle.css(props) }
        { props.children }
      </ApStyle>
    )
  }
})

export default ApBasicThemeStyle
